import styled from "styled-components";
import { NavLink } from "react-router-dom";
export const Search = styled.div`
    position: absolute;
    top: 168px;
    z-index: 450;
    left: 24px;
    width: 400px;
    background-color: white;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    @media (max-width: 1000px) {
      width: 300px;
    }
    @media (max-width: 800px) {
      top: 396px;
      left: 16px;
    }

`;
export const Container = styled.div`
  position: relative;
  top: 104px;
  display: flex;
    flex-wrap: wrap;
    @media (max-width: 1000px) {
      top: 98px;

flex-direction:column;
   
    }
`;
export const ContainerDane = styled.div`
  background-color: white;
  padding-top: 24px;
  padding-left: 24px;
  @media (max-width: 500px) {
    padding-left: 8px;
  }
  @media (max-width: 450px) {
    font-size: 16px;
  }
`;
export const Dane = styled.div`
  color: white;
  font-size: 20px;
  color: #feffff;
  font-family: "Exo 2", "Calibri", sans-serif;
  z-index: 600;
  @media (max-width: 450px) {
    font-size: 16px;
  }
`;
export const Menu = styled.ul`
  display: flex;
  position: absolute;
  top: 110px;
  z-index: 450;
  left: 24px;
`;
export const MenuElement = styled.li`
  &:nth-child(2) a {
    background-color: white;
    color: white;
    border-radius: 2px;
    margin-right: 4px;
    margin-left: 4px;
  }
`;
export const StyledLink = styled(NavLink).attrs()`
  width: 100%;
  padding: 8px 16px 8px 16px;
  text-decoration: none;
  color: #1f2961;
  font-size: 16px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  font-family: "Exo 2", "Calibri", sans-serif;
  border-radius: 2px;
  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14);
  background-color: white; ;
`;
export const DaneContent = styled.div`
  display: flex;
  @media (max-width: 600px) {
    flex-direction: column;
  }
`;
export const Dana = styled.div`
  height: 40px;
  
  @media (max-width: 450px) {
    font-size: 16px;
  }
`;

export const DanePolowa = styled.div`
  display: flex;
  flex-direction: column;
  margin-right: 36px;
  margin-bottom: 24px;
  &:nth-child(1) a {
    margin-right: 0;
  }
`;
export const Wykres = styled.img`
  max-width: 900px;
  height: auto;
  width: 100%;
`;
export const WykresContainer = styled.div`
  background-color: white;
  padding: 24px;
`;

export const DropDownList = styled.ul`
  cursor: pointer;
`;
export const ListElement = styled.li`
  display: none;
  cursor: pointer;
  font-family: "Exo 2", "Calibri", sans-serif;
  padding: 12px 10px;
  color: white;
`;
export const DropDownUp = styled.div`
  cursor: pointer;
  border: 1px solid #3f51b5;
  padding: 10px 20px;
  font-family: "Exo 2", "Calibri", sans-serif;
  color: white;
  border-radius: 5px;
`;
export const DropDownContainer = styled.div`
  margin-top: 8px;
  cursor: pointer;
  width: 300px;
  background: #3f51b5;
  margin-left: 24px;
  margin-top: 16px;
  padding: 14px 20px;
`;
export const Details = styled.div`
  cursor: pointer;
  font-weight: 600;
`;
export const DateText = styled.div`
font-weight: 300;
font-size: 16px;
line-height: 24px;
/* identical to box height, or 150% */
align-self: flex-start;
margin-bottom:8px;

color: #1F2961;
  font-family: "Exo 2", "Calibri", sans-serif;

`;
export const DateContainer = styled.div`
  display: flex;
  flex-direction:column;
  align-items: center;
  margin-left:40px;
  background:white;
  &:nth-child(2) {
    margin-left:24px;
  }
  @media (max-width: 1000px) {
    margin-left:24px;
    &:nth-child(2) {
    margin-left:16px;
  }
    }
`;
export const DatesContainer = styled.div`
  display: flex;



`;
export const GenerujWykres = styled.button`
background: #3F51B5;
box-shadow: 0px 8px 10px 1px rgba(0, 0, 0, 0.14);
border-radius: 2px;
width:141px;
height:40px;
border: none;
font-weight: 500;
font-size: 16px;
line-height: 24px;
margin-left:24px;
/* identical to box height, or 150% */
position:relative;
cursor: pointer;
top: 32px;

text-align: center;

color: #FFFFFF;
@media (max-width: 1000px) {
  height:32px;
  width:120px;
  margin: 0 auto;
  top: 2px;
  margin-bottom:18px;
    }
`;
export const Calendar = styled.img`
height:22px;
width:20px;
right: 50px;
    top: -30px;
position:relative;
@media (max-width: 1000px) {
  top: -27px;

    }
`;
