import React, { Component } from "react";
import {
  Container,
  DateText,
  DateContainer,
  DatesContainer,
  GenerujWykres,
  Calendar
} from "./WykresyStyle";
import $ from "jquery";
import DatePicker from "react-datepicker";
import { Bar, Line, Pie } from "react-chartjs-2";
import "react-datepicker/dist/react-datepicker.css";
import CalendarImg from "../../Assets/calendar-alt.svg";


class Wykresy extends Component {
  constructor(props) {
    super(props);
    this.state = {
      startDate: new Date().toISOString().replace(/T/g, "-"),
      endDate: new Date().toISOString().replace(/T/g, "-"),
      filteredArrayDaty: [],
      filteredArrayPomiary: [],
      datyPomiaru: [],
      filteredArray: [],
      chartData: {
        // Labels should be Date objects
        labels: [],
        datasets: [
          {
            fill: false,
            label: "Hałas [db]",
            data: [],
            borderColor: "#fe8b36",
            backgroundColor: "#fe8b36",
            lineTension: 0,
          },
        ],
      },
    };
    this.updateStates = this.updateStates.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleChange2 = this.handleChange2.bind(this);
    this.przygotujDane = this.przygotujDane.bind(this);
  }
  componentDidMount() {
    $.ajax({
      type: "GET" /*Informacja o tym, że dane będą pobierane*/,
      url:
        "http://localhost/phpsandbox/pobierz.php" /*Informacja, o tym jaki plik będzie przy tym wykorzystywany*/,
      contentType:
        "application/json; charset=utf-8" /*Informacja o formacie transferu danych*/,
      dataType: "json" /*Informacja o formacie transferu danych*/,

      /*Działania wykonywane w przypadku sukcesu*/
      success: (json) => {
        /*Funkcja zawiera parametr*/
        let data2 = [];
        let data1 = [];
        let data3 = [];
        var data4 = [];
        /*Pętla typu for...in języka Javascript na danych w formacie JSON*/
        for (var klucz in json) {
          var wiersz =
            json[klucz]; /*Kolejne przebiegi pętli wstawiają nowy klucz*/
          var czas = wiersz[0];

          var pomiar = wiersz[1];

          /*Ustalenie sposobu wyświetlania pobranych danych w bloku div*/
          //console.log(id, id1);
          if (czas.indexOf("-00-") < 0 && klucz % 60 === 0) {
            data2[klucz] = pomiar;
            data1[klucz] = czas;
            //console.log("klucz="+klucz);
            data3.push(czas, pomiar);
            data4.push(data3);

            data3 = [];
          }

          // console.log(data4);
        }
        console.log(data4);
        this.updateStates(data4);

        //console.log(data3);

        /*Dezaktywacja na określony czas przycisku wysyłającego - ten krok można pomninąć*/
        $("#pobierz").attr("disabled", true);
        setTimeout(function () {
          $("#pobierz").attr("disabled", false);
        }, 10000);
      },

      /*Działania wykonywane w przypadku błędu*/
      error: function (blad) {
        alert("Wystąpił błąd");
        console.log(
          blad
        ); /*Funkcja wyświetlająca informacje 
            o ewentualnym błędzie w konsoli przeglądarki*/
      },
    });
  }

  updateStates(data4) {
    this.setState({ datyPomiaru: data4 });
  }
  handleChange(date) {
    this.setState({ startDate: date.toISOString().replace(/T/g, "-") });
    console.log(date);
  }
  handleChange2(date) {
    this.setState({ endDate: date.toISOString().replace(/T/g, "-") });

    /*
  this.setState({
    startDate: date
  });*/
  }
  przygotujDane() {
    let chartData = { ...this.state.chartData };
    let filteredArrayDaty = [];
    let filteredArrayPomiary = [];
    this.state.datyPomiaru.forEach((item) => {
      console.log(item[0] + this.state.endDate);
      if (item[0] <= this.state.endDate && item[0] >= this.state.startDate) {
        item[0] = new Date(item[0]);
        item[1] = parseInt(item[1], 10);

        filteredArrayDaty.push(item[0]);
        filteredArrayPomiary.push(item[1]);
      }
    });
    chartData.labels = filteredArrayDaty;
    chartData.datasets[0].data = filteredArrayPomiary;
    this.setState({ chartData: chartData });
  }

  componentWillUnmount() {}

  render() {
    return (
      <Container>
        <DatesContainer><DateContainer>
          <DateText>Data rozpoczęcia</DateText>
          <DatePicker onChange={this.handleChange} selected={this.startDate} value={this.state.startDate.substring(0,10)}/>
          <Calendar src={CalendarImg}/>
        </DateContainer>
        <DateContainer>
          <DateText>Data zakończenia</DateText>
          <DatePicker onChange={this.handleChange2} selected={this.endDate} value={this.state.endDate.substring(0,10)}/>
          <Calendar src={CalendarImg}/>
        </DateContainer></DatesContainer>
        <GenerujWykres onClick={this.przygotujDane}>Pokaż wykresy</GenerujWykres>
        <Line
          data={this.state.chartData}
          options={{
            title: {
              display: this.props.displayTitle,
              text: "Largest Cities In " + this.props.location,
              fontSize: 25,
            },
            showLines: false,
            legend: {
              display: this.props.displayLegend,
              position: this.props.legendPosition,
            },
            scales: {
              xAxes: [
                {
                  type: "time",
                },
              ],
            },
          }}
        />
      </Container>
    );
  }
}
export default Wykresy;
